import share from '@/components/share';
export default {
	components: {
		share
	},
	data() {
		return {
			specClass: 'none',
			specSelected: [],
			productDetailDiscuss: {},
			favorite: true,
			shareList: [],
			imgList: [],
			desc: `
				<div style="width:100%">
					<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i4/479184430/O1CN01nCpuLc1iaz4bcSN17_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd2.alicdn.com/imgextra/i2/479184430/O1CN01gwbN931iaz4TzqzmG_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i3/479184430/O1CN018wVjQh1iaz4aupv1A_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd4.alicdn.com/imgextra/i4/479184430/O1CN01tWg4Us1iaz4auqelt_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd1.alicdn.com/imgextra/i1/479184430/O1CN01Tnm1rU1iaz4aVKcwP_!!479184430.jpg_400x400.jpg" />
				</div>
			`,
			specList: [],
			specChildList: [],
			productSelect: {},
			image: "",
			categaryId: "",
			styleId: ""

		};
	},
	async onLoad(options) {

		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = options.id;
		if (id) {
			this.getProductDetailDiscuss(id)
			//this.$api.msg(`点击了${id}`);
		}
		uni.request({
			url: this.myroot + "getProductStyleSize/" + id,
			success: (res) => {
				//console.log(res);
				this.productSelect = res.data.result;
				this.image = this.myroot + 'upload/' + this.productSelect.image
				var data = res.data.result
				var categary = data.categaries;
				for (var i = 0; i < categary.length; i++) {
					var t = {
						id: categary[i].c_id,
						name: categary[i].type
					}
					this.specList.push(t);
				}
				//console.log("this.specList")
				//console.log(this.specList)
				var styleList = data.styleList;
				for (var j = 0; j < styleList.length; j++) {
					var style = {
						name: styleList[j].styleName,
						id: styleList[j].s_id,
						pid: styleList[j].s_c_id,
					}
					this.specChildList.push(style);
				}
				//	console.log("this.specChildList")
				//	console.log(this.specChildList)

			}
		})


		// this.shareList = await this.$api.json('shareList');
	},
	methods: {
		//
		getProductDetailDiscuss(id) {
			uni.request({
				url: this.myroot + "getProductDetailDiscuss/" + id,
				success: (res) => {
					console.log("getProductDetailDiscuss")
					console.log(res);
					if (res.data.code == 0) {
						var src = res.data.result.image ? this.myroot + "/upload/" + res.data.result.image : ""
						this.imgList.push({
							src: src
						})
						src = res.data.result.image2 ? this.myroot + "/upload/" + res.data.result.image2 : ""
						this.imgList.push({
							src: src
						})
						src = res.data.result.image3 ? this.myroot + "/upload/" + res.data.result.image3 : ""
						this.imgList.push({
							src: src
						})

						this.productDetailDiscuss = res.data.result;

					}
				}
			})
		},
		//规格弹窗开关
		toggleSpec() {
			if (this.specClass === 'show') {
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			} else if (this.specClass === 'none') {
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(index, pid) {

			let list = this.specChildList;
			list.forEach(item => {
				if (item.pid === pid) {
					this.$set(item, 'selected', false);
				}
			})
			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = [];
			list.forEach(item => {
				if (item.selected === true) {
					this.specSelected.push(item);
				}
			})
			//	console.log("this.specSelected")
			//	console.log(this.specSelected)

		},
		//分享
		share() {
			this.$refs.share.toggleMask();
		},
		//收藏
		toFavorite() {
			this.favorite = !this.favorite;
		},
		buy() {
			if (this.specSelected.length < 1) {
				uni.showToast({
					title: "没有选型",
					icon: "none"
				})
				return;
			}
			uni.navigateTo({
				url: '/pages/order/createOrder?item=' + encodeURIComponent(JSON.stringify(this.specSelected)) + "&product=" +
					encodeURIComponent(JSON.stringify(this.productDetailDiscuss))
			})
		},
		stopPrevent() {}
	},

}
